import React from 'react';
import { injectIntl } from 'gatsby-plugin-intl';
import addToMailchimp from 'gatsby-plugin-mailchimp';

import pizza from '../assets/icons/pizza.png';
import arrow from '../assets/icons/arrow-right-2.png';

import styles from './Infographic.module.scss';

class Infographic extends React.Component {
  state = {
    mailchimp: {
      lang: null,
      email: null,
    },
    status: '',
  };

  componentDidMount() {
    console.log(this.props.intl.locale);
    this.setState({ mailchimp: { lang: this.props.intl.locale } });
  }

  _handleChange = (e) => {
    // console.log({
    //   [`${e.target.name}`]: e.target.value,
    // });
    // console.log(this.state);
    this.setState({
      mailchimp: {
        [`${e.target.name}`]: e.target.value,
        lang: this.props.intl.locale,
      },
    });
  };

  _handleSubmit = (e) => {
    e.preventDefault();

    console.log('submit', this.state);

    addToMailchimp(this.state.mailchimp.email, this.state.mailchimp)
      .then(({ msg, result }) => {
        // console.log('msg', `${result}: ${msg}`);

        if (result !== 'success') {
          throw msg;
        }
        this.setState({ status: msg });
        // alert(msg);
      })
      .catch((err) => {
        // console.log('err', err);
        this.setState({ status: err });
        // alert(err);
      });
  };

  render() {
    const { intl } = this.props;

    return (
      <section className={`${styles.info}`}>
        {/* Infographic */}
        <div className={`container container--grid`}>
          <div
            className={styles.lead}
            dangerouslySetInnerHTML={{
              __html: intl.formatHTMLMessage({ id: 'info_lead' }),
            }}
          />
          <div className={styles.chartContainer}>
            <p>{intl.formatMessage({ id: 'info_chart' })}</p>
            <img src={pizza} />
          </div>
          <div className={styles.score}>
            <p>
              <span className={styles.scoreNum}>
                {intl.formatMessage({ id: 'info_score_number' })}
              </span>
              <span className={styles.scoreText}>
                {intl.formatMessage({ id: 'info_score_text' })}
              </span>
              <span className={styles.scoreDetails}>
                {intl.formatMessage({ id: 'info_score_details' })}
              </span>
            </p>
          </div>
        </div>

        {/* Reminder */}
        <div className={`container container--grid ${styles.reminder}`}>
          <div
            className={styles.reminderTitle}
            dangerouslySetInnerHTML={{
              __html: intl.formatHTMLMessage({ id: 'reminder_title' }),
            }}
          />
          <div className={styles.reminderText}>
            <p>{intl.formatMessage({ id: 'reminder_text' })}</p>
          </div>
          <div className={styles.reminderArrow}>
            <img src={arrow} />
          </div>
          {/* <div className={styles.reminderCTA}>
            <a href="https://www.google.com" target="_blank">
              {intl.formatMessage({ id: 'reminder_cta' })}
            </a>
          </div> */}

          {/* mailchimp */}
          <div className={styles.formContainer}>
            <form className={styles.reminderForm} onSubmit={this._handleSubmit}>
              <input
                type="email"
                onChange={this._handleChange}
                placeholder="e-mail"
                name="email"
              />
              <button className={styles.btnSubmit} type="submit">
                
              </button>
              <p
                className={styles.status}
                dangerouslySetInnerHTML={{
                  __html: this.state.status,
                }}
              />
            </form>
          </div>
        </div>
      </section>
    );
  }
}

export default injectIntl(Infographic);
