import React, { useState } from 'react';
import { injectIntl } from 'gatsby-plugin-intl';
import ReactPlayer from 'react-player';
import ScrollIntoView from 'react-scroll-into-view';
import { Waypoint } from 'react-waypoint';

import styles from './VideoPreview.module.scss';

const VideoPreview = injectIntl(({ intl }) => {
  let player;
  const [modalIsOpen, setIsOpen] = useState(false);

  const onEnter = () => {
    if (player) player.playVideo();
  };

  const onLeave = () => {
    if (player) player.pauseVideo();
  };

  function openModal() {
    setIsOpen(true);
  }

  function closeModal() {
    setIsOpen(false);
  }

  return (
    <Waypoint
      onEnter={onEnter}
      onLeave={onLeave}
      bottomOffset="20%"
      topOffset="20%"
    >
      <section className={styles.videoPreview} id="video">
        <div className={`container`}>
          <h2 className={styles.title}>
            {intl.formatMessage({ id: 'hero_button_text' })}
          </h2>
          <div className={styles.playerContainer}>
            <ReactPlayer
              className={styles.player}
              playsinline
              width="100%"
              height="100%"
              url="https://youtu.be/FPw2qN21msw"
              config={{
                youtube: {
                  playerVars: {
                    showinfo: 0,
                    rel: 0,
                    controls: 1,
                    modestbranding: 1,
                    wmode: 'opaque',
                    cc_load_policy: 1,
                    cc_lang_pref: intl.locale,
                    hl: intl.locale,
                    persist_hl: 1,
                    playsinline: 1,
                    enablejsapi: 1,
                  },
                },
              }}
              onReady={(e) => {
                player = e.player.player.player;
              }}
            />
          </div>
          <ScrollIntoView selector="#video" smooth={true}>
            <button className={styles.btnCredits} onClick={openModal}>
              {intl.formatMessage({ id: 'credits_btn' })}
            </button>
          </ScrollIntoView>
          <div
            className={`${styles.modal} ${modalIsOpen ? styles.modalOpen : ''}`}
          >
            <div
              className={`container container--grid ${styles.modalContainer}`}
            >
              <div
                className={styles.credits}
                dangerouslySetInnerHTML={{
                  __html: intl.formatHTMLMessage({ id: 'credits' }),
                }}
              />
              <button className={styles.btnClose} onClick={closeModal}>
                x
              </button>
            </div>
          </div>
        </div>
      </section>
    </Waypoint>
  );
});

export default VideoPreview;
