import React from 'react';
import { injectIntl } from 'gatsby-plugin-intl';

import styles from './Footer.module.scss';

import logoEN from '../assets/logos/logo-en.svg';
import logoES from '../assets/logos/logo-es.svg';
import logoPT from '../assets/logos/logo-pt.svg';
import logoInfoAmazonia from '../assets/logos/infoamazonia_logo-white.png';
import shareFb from '../assets/logos/share-fb.png';
import shareIn from '../assets/logos/share-in.png';
import shareTw from '../assets/logos/share-tw.png';
import logoPulitzer from '../assets/logos/logo-pulitzer-center-white.png';
import logoIUCNI from '../assets/logos/logo-transparant-diapositief.png';

const Footer = injectIntl(({ intl }) => {
  const logos = {
    en: logoEN,
    es: logoES,
    pt: logoPT,
  };

  return (
    <footer className={`${styles.footer}`}>
      <div className={`container container--grid`}>
        {/* Signature */}
        <div className={styles.signature}>
          <img src={logos[intl.locale]} />
          <p>
            <span> 2020 </span>
            <img src={logoInfoAmazonia} />
          </p>
        </div>

        {/* Social */}
        <ul className={styles.social}>
          <li>
            <a
              href={intl.formatMessage({ id: 'facebook_url' })}
              target="_blank"
            >
              <img src={shareFb} />
            </a>
          </li>
          <li>
            <a
              href={intl.formatMessage({ id: 'instagram_url' })}
              target="_blank"
            >
              <img src={shareIn} />
            </a>
          </li>
          <li>
            <a href={intl.formatMessage({ id: 'twitter_url' })} target="_blank">
              <img src={shareTw} />
            </a>
          </li>
        </ul>

        {/* Supports */}
        <div className={styles.support}>
          <p>{intl.formatMessage({ id: 'support' })}</p>
          <figure>
            <img src={logoPulitzer} />
          </figure>
          <figure>
            <img src={logoIUCNI} />
          </figure>
        </div>
      </div>
    </footer>
  );
});

export default Footer;
