import React, { useState } from 'react';
import { injectIntl } from 'gatsby-plugin-intl';
import ScrollIntoView from 'react-scroll-into-view';

import HeroVideo from './HeroVideo';

import logoEN from '../assets/logos/logo-en.svg';
import logoES from '../assets/logos/logo-es.svg';
import logoPT from '../assets/logos/logo-pt.svg';

import clip3 from '../assets/videos/clip_3_crop.mp4';
import clip4 from '../assets/videos/clip_4_crop.mp4';
import clip6 from '../assets/videos/clip_6_crop.mp4';
import clip9 from '../assets/videos/clip_9_crop.mp4';
import clip11 from '../assets/videos/clip_11_crop.mp4';

import frame3 from '../assets/videos/clip_3_crop.jpg';
import frame4 from '../assets/videos/clip_4_crop.jpg';
import frame6 from '../assets/videos/clip_6_crop.jpg';
import frame9 from '../assets/videos/clip_9_crop.jpg';
import frame11 from '../assets/videos/clip_11_crop.jpg';

import arrow from '../assets/icons/arrow-right.png';

import styles from './Hero.module.scss';

const Hero = injectIntl(({ intl }) => {
  const logos = {
    en: logoEN,
    es: logoES,
    pt: logoPT,
  };

  const playersArr = [
    { clip: clip6, frame: frame6 },
    { clip: clip4, frame: frame4 },
    { clip: clip11, frame: frame11 },
    { clip: clip3, frame: frame3 },
    { clip: clip9, frame: frame9 },
  ];

  const [videoPlaying, setVideoPlaying] = useState(0);
  const [mouse, setMouse] = useState({
    left: 'auto',
    centerLeft: '-50%',
    top: 'auto',
    centerTop: '-50%',
  });
  const [mouseOn, setMouseOn] = useState(false);

  const parallax = (e) => {
    e.persist();
    const bounds = e.currentTarget.getBoundingClientRect();

    const elPos = { x: bounds.left, y: bounds.top };
    const elCenter = {
      x: bounds.left + bounds.width / 2,
      y: bounds.top + bounds.height / 2,
    };
    // const mousePos = { x: e.clientX, y: e.clientY }
    const mousePosRelative = {
      x: e.clientX - bounds.left,
      y: e.clientY - bounds.top,
    };
    const mousePercentage = {
      x: mousePosRelative.x / bounds.width,
      y: mousePosRelative.y / bounds.height,
    };

    const multiplier = 0.02;

    const relativePosition = {
      left: `${(mousePercentage.x * 100 - 50) * -multiplier}%`,
      top: `${(mousePercentage.y * 100 - 50) * -multiplier}%`,
      centerLeft: `${(mousePercentage.x * 100 - 50) * multiplier - 50}%`,
      centerTop: `${(mousePercentage.y * 100 - 50) * multiplier - 50}%`,
    };

    setMouse({
      left: relativePosition.left,
      top: relativePosition.top,
      centerLeft: relativePosition.centerLeft,
      centerTop: relativePosition.centerTop,
    });
  };

  const lockParallax = () => {
    setMouseOn(true);
  };

  const releaseParallax = () => {
    setMouseOn(false);
  };

  return (
    <header className={`${styles.header}`}>
      <div
        className={styles.parallaxContainer}
        onMouseMove={parallax}
        onMouseEnter={lockParallax}
        onMouseLeave={releaseParallax}
      >
        {/* PLAYERS */}
        <div
          className={`${styles.players} ${
            !mouseOn ? styles.autoPosition : ''
          } container container--grid`}
          style={{
            left: mouse.left,
            top: mouse.top,
          }}
        >
          {playersArr.map((p, i) => (
            <HeroVideo
              className={`${styles.playerFlexContainer} `}
              key={i}
              src={p.clip}
              frame={p.frame}
              playing={i === videoPlaying ? true : false}
              onEnded={() => {
                let n = videoPlaying;
                while (n === videoPlaying) {
                  n = Math.round(Math.random() * (playersArr.length - 1));
                }
                setVideoPlaying(n);
              }}
            />
          ))}
        </div>

        {/* TITLE */}
        <div
          className={`container ${styles.titleContainer} ${
            !mouseOn ? styles.autoPosition : ''
          }`}
          style={{
            transform: `translate(${mouse.centerLeft}, ${mouse.centerTop})`,
          }}
        >
          <h1 className={styles.title}>
            <span className="sr-only">
              {intl.formatMessage({ id: 'title' })}
            </span>
            <img className={styles.logo} src={logos[intl.locale]} />
          </h1>
        </div>

        {/* LEAD */}
        <div
          className={`container ${styles.leadContainer} ${
            !mouseOn ? styles.autoPosition : ''
          }`}
          style={{
            transform: `translate(${mouse.centerLeft}, ${mouse.top})`,
          }}
        >
          <dl
            className={styles.credits}
            dangerouslySetInnerHTML={{
              __html: intl.formatHTMLMessage({ id: 'hero_credits' }),
            }}
          />
          <p className={styles.leadText}>
            {intl.formatMessage({ id: 'hero_lead' })}
          </p>
          <img src={arrow} className={styles.arrow} />
          <ScrollIntoView
            className={styles.cta}
            selector="#video"
            smooth={true}
          >
            <button>{intl.formatMessage({ id: 'hero_button_text' })}</button>
          </ScrollIntoView>
        </div>
      </div>
    </header>
  );
});

export default Hero;
