import React, { useState } from 'react';

import logoInfoAmazonia from '../assets/logos/infoamazonia_logo-white.png';
import {
  changeLocale,
  injectIntl,
  IntlContextConsumer,
  Link,
} from 'gatsby-plugin-intl';

import styles from './Header.module.scss';

const languageName = {
  en: 'EN',
  es: 'ES',
  pt: 'PT',
};

const Header = injectIntl(({ intl }) => {
  const [open, setOpen] = useState(false);

  return (
    <header className={styles.header}>
      <div className={`container ${styles.container}`}>
        <a
          target="_blank"
          rel="noopener noreferrer"
          href={`${intl.messages.signatureUrl}${intl.locale}`}
        >
          {/* {intl.formatMessage({ id: 'signature' })} */}
          <img className={styles.logo} src={logoInfoAmazonia} />
        </a>

        {/* Language switcher */}
        <nav className={`${styles.nav} ${open ? styles.navOpen : ''}`}>
          <ul>
            <IntlContextConsumer>
              {({ languages, language: currentLocale }) =>
                languages.map((language) => (
                  <li
                    key={language}
                    className={currentLocale === language ? styles.active : ''}
                  >
                    {currentLocale === language ? (
                      <span className={styles.langText}>
                        {languageName[language]}
                      </span>
                    ) : (
                      <a
                        className={`${styles.langText} ${styles.langLink}`}
                        rel="alternate"
                        hrefLang={language}
                        key={language}
                        onClick={() => {
                          changeLocale(language);
                          setOpen(false);
                        }}
                      >
                        {languageName[language]}
                      </a>
                    )}
                  </li>
                ))
              }
            </IntlContextConsumer>
          </ul>
          <button
            className={styles.toggle}
            onClick={() => {
              setOpen(!open);
            }}
          >
            <svg
              version="1.1"
              xmlns="http://www.w3.org/2000/svg"
              x="0px"
              y="0px"
              width="12px"
              height="12px"
              viewBox="0 0 18.3 16.8"
            >
              <path d="M4.1,0l4.8,11.6L14.2,0h4.1L9.8,16.8H7.4L0,0H4.1z" />
            </svg>
          </button>
        </nav>
      </div>
    </header>
  );
});

export default Header;
